@tailwind base;
@tailwind components;
@tailwind utilities;

/* Custom CSS to ensure white text visibility */
.text-white-force {
  color: rgb(255 255 255) !important;
}

/* Fix for text on gradient backgrounds */
.gradient-text-container h1,
.gradient-text-container .text-white {
  position: relative;
  z-index: 10;
  color: rgb(255 255 255) !important;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
}

/* Manual text shadow utility */
.text-shadow {
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
}